import Container from '../container';
import WordSlider from '../word-slider';

export default () => (
  <div className="slider-container">
    <WordSlider duration={1800}>
      <span>Deep Learning</span>
      <span>3D Vision</span>
      <span>Building Reconstruction</span>
      <span>Object Recognition</span>
      <span>Autopilot</span>
      <span>Urban Design</span>
      <span>Benchmark</span>
      <span>Data Scientist</span>
    </WordSlider>
    <style jsx>{`
      .slider-container {
        margin: auto;
        margin-top: 0;
        margin-bottom: -1rem;
        line-height: 1.4em;
        white-space: nowrap;
      }
    `}</style>
  </div>
);
